.page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.header {
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  a {
    font-size: 24px;
    color: #adadad;
    font-weight: 600;
    text-decoration: none;
  }
}

.artigo {
  background-color: #f5f5f7;
  width: 100%;
  height: 100%;
  padding: 32px 20vw;
  display: flex;
  flex-direction: column;
  gap: 32px;
  overflow: auto;
}

.wrapperTitulo {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.titulo {
  margin: 0;
  font-size: 32px;
}

.desc {
  color: #adadad;
  display: flex;
  gap: 8px;
}

.conteudo {
  display: flex;
  flex-direction: column;
  gap: 32px;
  overflow: auto;
  height: 100%;
  overflow-x: hidden;
  scrollbar-width: none;
  user-select: none;
}

.erro {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}